import React, { useState, useMemo, useEffect } from 'react';
import type { GetStaticProps } from 'next';
import Head from 'next/head';
import { useRouter } from 'next/router';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import { NeoButton } from '../components/NeoButton';
import { NeoCard } from '../components/NeoCard';
import { Reveal } from '../components/Reveal';
import { BlogPost } from '../types';
import { CONTENT } from '../data/content';
import { getAllPosts } from '../lib/posts';
import { useLanguage } from '../contexts/LanguageContext';
import { TRANSLATIONS } from '../data/translations';

type BlogPageProps = {
  content: typeof CONTENT;
};

export default function Blog({ content }: BlogPageProps) {
  const { language } = useLanguage();
  const router = useRouter();
  const t = TRANSLATIONS[language];
  const posts = content[language].blog;

  const meta = {
    en: {
      title: 'Journal | Heiheihoho\'s Portfolio',
      description: 'Long-form thoughts on design trends, voxel engines, and Tailwind experiments.'
    },
    zh: {
      title: '博客 | Heiheihoho\'s 文章',
      description: '记录工作心得、技术分享与生活感悟的长文笔记。'
    }
  }[language];

  const canonical = `https://neo-brutalism.example.com${router.asPath}`;

  const [selectedPost, setSelectedPost] = useState<BlogPost | null>(null);
  const [selectedTag, setSelectedTag] = useState<string>('ALL');

  const allTags = useMemo(() => {
    const tags = new Set<string>();
    // 按照 date 倒序
    posts.toSorted((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime()).forEach(post => post.tags.forEach(tag => tags.add(tag)));
    return ['ALL', ...Array.from(tags)];
  }, [posts]);

  useEffect(() => {
    if (selectedTag !== 'ALL' && !allTags.includes(selectedTag)) {
      setSelectedTag('ALL');
    }
  }, [language, allTags, selectedTag]);

  const filteredPosts = useMemo(() => {
    const base =
      selectedTag === 'ALL' ? posts : posts.filter(post => post.tags.includes(selectedTag));

    return [...base].sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  }, [selectedTag, posts]);

  if (selectedPost) {
    return (
      <div className="animate-fade-in-up">
        <Head>
          <title>{selectedPost.title} | {meta.title}</title>
          <meta name="description" content={selectedPost.excerpt} />
          <link rel="canonical" href={canonical} />
          <meta property="og:type" content="article" />
          <meta property="og:title" content={selectedPost.title} />
          <meta property="og:description" content={selectedPost.excerpt} />
          <meta property="og:url" content={canonical} />
          <meta property="og:image" content="https://picsum.photos/1200/630?blog" />
          <meta name="twitter:card" content="summary_large_image" />
          <meta name="twitter:title" content={selectedPost.title} />
          <meta name="twitter:description" content={selectedPost.excerpt} />
          <meta name="twitter:image" content="https://picsum.photos/1200/630?blog" />
        </Head>
        <NeoButton onClick={() => setSelectedPost(null)} variant="secondary" className="mb-8">
          {t.blog_back}
        </NeoButton>
        <NeoCard className="p-8 md:p-12 bg-white">
          <div className="border-b-4 border-black pb-6 mb-8">
            <div className="flex flex-wrap items-center gap-4 mb-4 text-sm font-bold uppercase tracking-wider text-gray-500">
              <span>{selectedPost.date}</span>
              <span>•</span>
              <span>
                {selectedPost.readTime} {t.blog_read_time}
              </span>
              <span className="hidden sm:inline">•</span>
              <div className="flex gap-2">
                {selectedPost.tags.map(tag => (
                  <span key={tag} className="bg-yellow-200 border border-black px-1.5 py-0.5 text-[10px] text-black">
                    {tag}
                  </span>
                ))}
              </div>
            </div>
            <h1 className="text-4xl md:text-6xl font-black uppercase leading-none">{selectedPost.title}</h1>
          </div>

          <div className="prose prose-xl prose-black max-w-none font-medium markdown-content">
            <ReactMarkdown
              remarkPlugins={[remarkGfm]}
              components={{
                h1: ({ children }) => <h1 className="text-4xl font-bold mt-8 mb-4">{children}</h1>,
                h2: ({ children }) => <h2 className="text-2xl font-bold mt-6 mb-3 border-l-4 border-yellow-400 pl-4">{children}</h2>,
                h3: ({ children }) => <h3 className="text-xl font-bold mt-4 mb-2">{children}</h3>,
                p: ({ children }) => <p className="mb-4 text-lg leading-relaxed">{children}</p>,
                blockquote: ({ children }) => <blockquote className="bg-gray-100 p-4 border-l-4 border-black italic my-4">{children}</blockquote>,
                ul: ({ children }) => <ul className="ml-4 list-disc mb-4">{children}</ul>,
                ol: ({ children }) => <ol className="ml-4 list-decimal mb-4">{children}</ol>,
                li: ({ children }) => <li className="font-bold mb-2">{children}</li>,
                code: ({ inline, className, children, ...props }: any) => {
                  if (inline) {
                    return <code className="bg-gray-100 px-1 py-0.5 rounded border border-black text-sm font-mono" {...props}>{children}</code>;
                  }
                  return (
                    <pre className="bg-black text-white p-4 border-4 border-black my-4 overflow-x-auto">
                      <code className="text-sm font-mono" {...props}>{children}</code>
                    </pre>
                  );
                },
                a: ({ href, children }) => <a href={href} className="text-blue-600 hover:underline font-bold" target="_blank" rel="noopener noreferrer">{children}</a>,
                strong: ({ children }) => <strong className="font-black">{children}</strong>,
                em: ({ children }) => <em className="italic">{children}</em>
              }}
            >
              {selectedPost.content}
            </ReactMarkdown>
          </div>
        </NeoCard>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto">
      <Head>
        <title>{meta.title}</title>
        <meta name="description" content={meta.description} />
        <link rel="canonical" href={canonical} />
        <meta property="og:type" content="website" />
        <meta property="og:title" content={meta.title} />
        <meta property="og:description" content={meta.description} />
        <meta property="og:url" content={canonical} />
        <meta property="og:image" content="https://picsum.photos/1200/630?blog" />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={meta.title} />
        <meta name="twitter:description" content={meta.description} />
        <meta name="twitter:image" content="https://picsum.photos/1200/630?blog" />
      </Head>
      <Reveal>
        <div className="mb-8 text-center">
          <h1 className="text-6xl font-black uppercase mb-4">{t.blog_title}</h1>
          <div className="h-2 bg-black w-24 mx-auto mb-8" />

          <div className="flex flex-wrap justify-center gap-2">
            {allTags.map(tag => (
              <button
                key={tag}
                onClick={() => setSelectedTag(tag)}
                className={`
                  px-3 py-1 font-bold uppercase text-xs border-2 border-black transition-all duration-200
                  ${selectedTag === tag
                    ? 'bg-black text-white shadow-[4px_4px_0px_0px_rgba(100,100,100,1)] translate-x-[-2px] translate-y-[-2px]'
                    : 'bg-white text-black hover:bg-yellow-200 shadow-[2px_2px_0px_0px_rgba(0,0,0,1)] hover:shadow-[4px_4px_0px_0px_rgba(0,0,0,1)] hover:translate-x-[-1px] hover:translate-y-[-1px]'}
                `}
              >
                {tag}
              </button>
            ))}
          </div>
        </div>
      </Reveal>

      <div className="space-y-6 min-h-[50vh]">
        {filteredPosts.length > 0 ? (
          filteredPosts.map((post, index) => (
            <Reveal key={post.id} delay={index * 100}>
              <NeoCard className="group cursor-pointer bg-white" hoverEffect>
                <div onClick={() => setSelectedPost(post)} className="p-6 md:p-8 flex flex-col md:flex-row gap-6 md:items-center">
                  <div className="flex-grow">
                    <div className="flex flex-wrap gap-2 mb-3 items-center text-xs font-bold uppercase tracking-wider text-gray-500">
                      <span className="bg-black text-white px-2 py-0.5">{post.date}</span>
                      <span className="py-0.5">{post.readTime}</span>
                      <div className="hidden md:block w-1 h-1 bg-gray-400 rounded-full mx-1" />
                      <div className="flex gap-2">
                        {post.tags.map(tag => (
                          <span key={tag} className="bg-gray-100 border border-black px-1.5 py-0.5 text-[10px] text-black group-hover:bg-yellow-200 transition-colors">
                            {tag}
                          </span>
                        ))}
                      </div>
                    </div>
                    <h2 className="text-2xl md:text-3xl font-black uppercase mb-3 group-hover:text-blue-600 transition-colors">
                      {post.title}
                    </h2>
                    <p className="text-gray-600 font-medium md:w-3/4">{post.excerpt}</p>
                  </div>
                  <div className="flex-shrink-0">
                    <div className="w-12 h-12 border-2 border-black flex items-center justify-center bg-yellow-300 group-hover:bg-yellow-400 transition-colors shadow-[2px_2px_0px_0px_rgba(0,0,0,1)] group-hover:translate-x-1 group-hover:translate-y-1 group-hover:shadow-none">
                      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={3} stroke="currentColor" className="w-6 h-6">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M13.5 4.5L21 12m0 0l-7.5 7.5M21 12H3" />
                      </svg>
                    </div>
                  </div>
                </div>
              </NeoCard>
            </Reveal>
          ))
        ) : (
          <Reveal>
            <div className="text-center py-20 border-2 border-dashed border-gray-400 text-gray-500 font-bold uppercase">
              {t.blog_no_results} "{selectedTag}"
            </div>
          </Reveal>
        )}
      </div>
    </div>
  );
}

export const getStaticProps: GetStaticProps<BlogPageProps> = async () => {
  // Read markdown posts
  const blogPostsEn = getAllPosts('en');
  const blogPostsZh = getAllPosts('zh');

  // Merge with existing content (projects, gallery)
  const content = {
    en: {
      ...CONTENT.en,
      blog: blogPostsEn
    },
    zh: {
      ...CONTENT.zh,
      blog: blogPostsZh
    }
  };

  return {
    props: {
      content
    }
  };
};
