import fs from 'fs';
import path from 'path';
import matter from 'gray-matter';
import { BlogPost } from '../types';

const postsDirectory = path.join(process.cwd(), 'posts');

export function getPostSlugs(lang: 'en' | 'zh'): string[] {
  const langDir = path.join(postsDirectory, lang);
  if (!fs.existsSync(langDir)) {
    return [];
  }
  return fs.readdirSync(langDir)
    .filter(file => file.endsWith('.md'))
    .map(file => file.replace(/\.md$/, ''));
}

export function getPostBySlug(slug: string, lang: 'en' | 'zh'): BlogPost | null {
  const langDir = path.join(postsDirectory, lang);
  const fullPath = path.join(langDir, `${slug}.md`);

  if (!fs.existsSync(fullPath)) {
    return null;
  }

  const fileContents = fs.readFileSync(fullPath, 'utf8');
  const { data, content } = matter(fileContents);

  return {
    id: data.id,
    title: data.title,
    date: data.date,
    readTime: data.readTime,
    excerpt: data.excerpt,
    tags: data.tags || [],
    content: content.trim()
  } as BlogPost;
}

export function getAllPosts(lang: 'en' | 'zh'): BlogPost[] {
  const slugs = getPostSlugs(lang);
  const posts = slugs
    .map(slug => getPostBySlug(slug, lang))
    .filter((post): post is BlogPost => post !== null)
    .sort((a, b) => {
      // Sort by date descending
      return new Date(b.date).getTime() - new Date(a.date).getTime();
    });

  return posts;
}

